import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { format } from 'date-fns';
import {
    Container,
    Stack,
    Card,
    Box, Grid, Alert,
} from '@mui/material';
import swal from 'sweetalert';
import { useForm } from 'react-hook-form';
import { LoadingButton } from '@mui/lab';
import { PATH_DASHBOARD } from '../../routes/paths';
import useSettings from '../../hooks/useSettings';
import axios from '../../utils/axios';
import useAuth from '../../hooks/useAuth';
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { FormProvider, RHFSelect, RHFTextField, RHFSwitch } from '../../components/hook-form';

export default function UpdateKYC() {

    const NewUserSchema = Yup.object().shape({
        date_of_birth: Yup.date().required('Date of Birth is required'),
        bvn_number: Yup.string().required('Number is required'),
    });
    const AccessToken = window.localStorage.getItem('accessToken');
    const { themeStretch } = useSettings();
    const { user } = useAuth();
    const methods = useForm({
        resolver: yupResolver(NewUserSchema),
    });
    const {
        handleSubmit,
        formState: { errors, isSubmitting },
    } = methods;

    const onSubmit = async (data) => {

        try {
            const isConfirmed = await swal({
                title: 'Confirmation',
                text: `Please check the information before sending it. You have one free trial attempt. If it doesn't work, you will be charged for the next attempt`,
                icon: 'warning',
                buttons: ['Cancel', 'Confirm'],
                dangerMode: true,
            });
            if (isConfirmed) {
                const formattedDate = format(new Date(data.date_of_birth), 'dd-MMM-yyyy');
                const resp = await axios.post('/api/update-kyc-here/adex/secure', {
                    data: { ...data, date_of_birth: formattedDate }, token: AccessToken,
                });
                swal('cool', 'KYC Approved', 'success');
                window.location.reload();
            }
        } catch (e) {
            console.log(e);
            if (e?.data?.message !== undefined) {
                swal('Oops', `${e?.data?.message}`, 'error');
            } else if (e?.message !== undefined) {
                swal('Oops', `${e?.message}`, 'error');
            } else {
                swal('Oops', 'Something went wrong', 'error');
            }
        }
    }
    return (
        <Page title="Update KYC">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading={'Update KYC'}
                    links={[
                        { name: 'Dashboard', href: PATH_DASHBOARD.root },
                        { name: 'Fund Wallet', href: PATH_DASHBOARD.fund.account },
                    ]}
                />
                {!user?.is_bvn ? <>
                    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                        <Grid container spacing={3}>
                            <Grid item xs={12} md={8}>
                                {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
                                <Card sx={{ p: 3 }}>
                                    <Box
                                        sx={{
                                            display: 'grid',
                                            columnGap: 1,
                                            rowGap: 3,
                                            gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
                                        }}
                                    >
                                        <RHFTextField name="bvn_number" label="BVN" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
                                        <RHFTextField
                                            name="date_of_birth"
                                            label="Date of Birth Linked to BVN"
                                            type="date"
                                            inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}
                                            InputLabelProps={{ shrink: true }}
                                            fullWidth
                                        />
                                    </Box>
                                    <Stack alignItems="flex-end" sx={{ mt: 3 }}>
                                        <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                                            Proceed
                                        </LoadingButton>
                                    </Stack>
                                </Card>
                            </Grid>
                        </Grid>
                    </FormProvider>
                </> : <></>}
            </Container>
        </Page>
    );

}